import React, {useState} from "react"
import Row from './Components/row'
import "./calculator.css"
function Calculator(){
    let textField = ""
    let [output, setOutput] = useState("")
    let [fieldValue, setfieldValue] = useState(textField)
    function getClickVal(clickVal){
        if(clickVal === "="){
            fieldValue = eval(fieldValue)
            setOutput(fieldValue)
        }
        else if(clickVal === "c"){
            fieldValue = fieldValue.slice(0,-1)
            setfieldValue(fieldValue)
        }
        else if(clickVal === "CE"){
            setfieldValue("")
            setOutput("")
        }
        else{
            setfieldValue(fieldValue + clickVal)
        }
    }
    return(
        <div className="calculator">
            <input type="text" value={output} /><br/>
            <input type="text" value={fieldValue} /><br/>
            <Row value1="CE" value2="c" value3="=" value4="/" getClick={getClickVal} />
            <Row value1="7" value2="8" value3="9" value4="*" getClick={getClickVal} />
            <Row value1="4" value2="5" value3="6" value4="-" getClick={getClickVal} />
            <Row value1="1" value2="2" value3="3" value4="+" getClick={getClickVal} />
            <Row value1="00" value2="0" value3="." value4="%" getClick={getClickVal} />
        </div>
    )
}
export default Calculator