import './row.css'
import Box from './box'

const Row = (props) => {
    function getBoxVal(boxVal){
        props.getClick(boxVal)
    }
    return(
        <div className="row">
            <Box value={props.value1} getVal={getBoxVal} />
            <Box value={props.value2} getVal={getBoxVal} />
            <Box value={props.value3} getVal={getBoxVal} />
            <Box value={props.value4} getVal={getBoxVal} />
        </div>
    )
}
export default Row